@media screen and (min-width: 600px) and (max-width: 900px) {

}
@media screen and (min-width: 900px) {
#footer{    
    width: 100%;
    margin: 0 auto;
    background: #313332; 
    background-size: 100% auto;
    display: flex;
    background-attachment: scroll;             
}
#footerContainer{
    display: flex;
    flex-direction: column;    
    justify-content: center;
    width: 100%;
    font-family: "lato Regular", Helvetica, sans-serif;  
    font-size: 17px;
    color: white;
    padding-top: 21px;    
}
.footerSuperior{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 100%;
}
.footerSuperior li { 
    list-style-type: none; 
    color: #deaf06;
}
.footerSuperior a {
    font-size: 21px; 
    text-decoration: none;
}
.footerSuperior a:link{
    color: white;    
}
.footerSuperior a:visited{
    color: white;    
}
.footerSuperior a:hover{
    background-color: #006699;    
    text-decoration: underline;
}
.masLinks{
    display: flex;
    flex-direction: column;
    width: 33%;
    padding: 15px 0px 35px 121px;    
    justify-content: space-between;
    justify-items: center;
}
.siguenosRedes{
    display: flex;
    flex-direction: column;
    width: 25%;
    padding-right: 48px;
    padding-left: 48px;
    margin-top: -15px;
}
.datosContacto{
    display: flex;
    flex-direction: column;
    width: 42%;
    margin-top: -15px;
}
.listaFooter{
    margin-top: -21px;
    text-align: justify;
}
.siguenosRedes .listaFooter li{
    border-bottom: #e9ebe6;
    border-bottom-style: solid;
}
.footerInferior{
    width: 100%;
    text-align: center;
    font-size: 21px;
    border-top: blanchedalmond;
    border-top-style: solid;
}   
}