/* height: 700px; */
.blogTitulo{
    background-color: #009999; 
    height: 220px;
    width: 100%;
}
.mainTitulo{
    color: #e9ebe6;
    font-size: 84px;
    margin: auto 0%;
}
.mainTitulo h1{
    margin: auto 0%;
}
.testimonioPropietarios{
    background-color: #e9ebe6;
}
.testimoniosTitulo p{
    margin-top: 0px;
    padding-top: 48px;
    font-size: 33px;
}
.mainCol{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 748px;
    padding-top: 3em;
}
.experienciasContainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%; 
}
.col{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2em;
    width: 30%;
}
.row{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 84%;
    /* height: 160px; */
    padding: 2em;
}
